.sidebar {
    display: flex;
    flex: 1;
    flex-direction: column;
    justify-content: right;

    padding: 80px;

    row-gap: 30px;

    min-width: 360px;
    max-width: 360px;
}

.sidebar > .header {
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    font-family: "Noto Serif", serif;
    color: black;
}
.sidebar > .header:hover {
    cursor: pointer;
}

.sidebar > .header > .title {
    margin: 0;
    font-weight: 400;
    font-size: 36px;
}

.sidebar > .header > .description {
    font-size: 18px;
    font-family: "Noto Serif", serif;
    font-weight: 300;
    margin: 0;
    width: 100%;
}

.sidebar > .content {
    display: flex;
    flex: 1;
    flex-direction: column;
    row-gap: 30px;
}

.sidebar > .content > .section {
    display: flex;
    flex-direction: column;
    font-family: "Noto Serif", serif;
    font-weight: 400;
    row-gap: 10px;
    align-items: baseline;

    column-gap: 20px;
}

.sidebar > .content > .section > .header {
    display: flex;
    align-items: baseline;
    flex-direction: row;
    column-gap: 20px;

    font-family: "Noto Serif", serif;
    font-weight: 400;
    font-size: 24px;
}

.sidebar > .content > .section > .header > .title {
    font-size: 24px;
}

.sidebar > .content > .section > .header > .clear-button {
    display: flex;
    flex-direction: row;

    column-gap: 10px;

    align-items: center;

    color: gray;

    font-family: "JetBrains Mono", monospace;
    font-size: 16px;
    font-weight: 200;

    cursor: pointer;

    transition: color 0.15s;
}
.sidebar > .content > .section > .header > .clear-button:hover {
    color: gainsboro;
}
.sidebar > .content > .section > .header > .clear-button > i {
    color: gainsboro;
    transition: color 0.15s;
}
.sidebar > .content > .section > .header > .clear-button:hover > i {
    color: red;
}

.sidebar > .content > .section > .elements,
.sidebar > .content > .section > .selected-elements {
    display: flex;
    flex-direction: row;
    align-items: baseline;

    flex-wrap: wrap;
    column-gap: 15px;

    color: gray;
}

.sidebar > .content > .section .element {
    font-family: "JetBrains Mono", monospace;
    font-weight: 200;
    font-size: 16px;

    display: flex;
    align-items: baseline;
    flex-direction: row;
    padding: 5px;
    column-gap: 5px;

    color: gray;
    text-decoration: none;
    transition: color 0.15s;
}
.sidebar > .content > .section .element > i {
    transition: color 0.15s;
    color: gainsboro;
}
.sidebar > .content > .section .element.selected {
    color: orange;
}
.sidebar > .content > .section .element.selected > i {
    color: gainsboro;
}
.sidebar > .content > .section .element:hover {
    cursor: pointer;
    color: gainsboro;
}
.sidebar > .content > .section .element:hover > i {
    color: red;
}

/* The Archive section should have one element per row */
.sidebar > .content > .section > .elements.archive {
    flex-direction: column;
}

.sidebar > .footer {
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

.sidebar > .footer > .socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
    column-gap: 40px;
}
.sidebar > .footer > .socials i {
    font-size: 25px;
    width: 25px;
    height: 25px;
    text-align: center;
    transition: 0.15s;
    color: gray;
}
.sidebar > .footer > .socials i:hover {
    cursor: pointer;
    color: #9c9c9c;
}
.sidebar > .footer > .copyright {
    font-family: "Noto Serif", serif;
    font-weight: 300;
    font-size: 12px;
    color: gray;
    margin: 0;
    text-align: center;
}`, "",{"version":3,"sources":["webpack://./src/pages/landing/sidebar.css"],"names":[],"mappings":";AACA;IACI,aAAa;IACb,OAAO;IACP,sBAAsB;IACtB,sBAAsB;;IAEtB,aAAa;;IAEb,aAAa;;IAEb,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,gCAAgC;IAChC,YAAY;AAChB;AACA;IACI,eAAe;AACnB;;AAEA;IACI,SAAS;IACT,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,eAAe;IACf,gCAAgC;IAChC,gBAAgB;IAChB,SAAS;IACT,WAAW;AACf;;AAEA;IACI,aAAa;IACb,OAAO;IACP,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,gCAAgC;IAChC,gBAAgB;IAChB,aAAa;IACb,qBAAqB;;IAErB,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,qBAAqB;IACrB,mBAAmB;IACnB,gBAAgB;;IAEhB,gCAAgC;IAChC,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,mBAAmB;;IAEnB,gBAAgB;;IAEhB,mBAAmB;;IAEnB,WAAW;;IAEX,wCAAwC;IACxC,eAAe;IACf,gBAAgB;;IAEhB,eAAe;;IAEf,uBAAuB;AAC3B;AACA;IACI,gBAAgB;AACpB;AACA;IACI,gBAAgB;IAChB,uBAAuB;AAC3B;AACA;IACI,UAAU;AACd;;AAEA;;IAEI,aAAa;IACb,mBAAmB;IACnB,qBAAqB;;IAErB,eAAe;IACf,gBAAgB;;IAEhB,WAAW;AACf;;AAEA;IACI,wCAAwC;IACxC,gBAAgB;IAChB,eAAe;;IAEf,aAAa;IACb,qBAAqB;IACrB,mBAAmB;IACnB,YAAY;IACZ,eAAe;;IAEf,WAAW;IACX,qBAAqB;IACrB,uBAAuB;AAC3B;AACA;IACI,uBAAuB;IACvB,gBAAgB;AACpB;AACA;IACI,aAAa;AACjB;AACA;IACI,gBAAgB;AACpB;AACA;IACI,eAAe;IACf,gBAAgB;AACpB;AACA;IACI,UAAU;AACd;;AAEA,wDAAwD;AACxD;IACI,sBAAsB;AAC1B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,gBAAgB;AACpB;AACA;IACI,eAAe;IACf,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,iBAAiB;IACjB,WAAW;AACf;AACA;IACI,eAAe;IACf,cAAc;AAClB;AACA;IACI,gCAAgC;IAChC,gBAAgB;IAChB,eAAe;IACf,WAAW;IACX,SAAS;IACT,kBAAkB;AACtB","sourcesContent":["\r\n.sidebar {\r\n    display: flex;\r\n    flex: 1;\r\n    flex-direction: column;\r\n    justify-content: right;\r\n\r\n    padding: 80px;\r\n\r\n    row-gap: 30px;\r\n\r\n    min-width: 360px;\r\n    max-width: 360px;\r\n}\r\n\r\n.sidebar > .header {\r\n    display: flex;\r\n    flex-direction: column;\r\n    row-gap: 10px;\r\n    font-family: \"Noto Serif\", serif;\r\n    color: black;\r\n}\r\n.s